import { useState, useEffect, useMemo } from 'react';
import { useEthers } from "@usedapp/core";
const { MerkleTree } = require('merkletreejs');
const keccak256 = require('keccak256');
function computeProof(account, data) {
    let isWhitelisted = false;
    if (data.indexOf(account) > -1) {
        isWhitelisted = true;
    }
    let hexProof = []
    if (isWhitelisted) {
        const leafNodes = data.map(addr => keccak256(addr));
        const merkleTree = new MerkleTree(leafNodes, keccak256, { sortPairs: true });
        const claimingAddress = keccak256(account); //metamask test wallet
        hexProof = merkleTree.getHexProof(claimingAddress);
    }
    return { whitelisted: isWhitelisted, proof: hexProof };
}
export function useMerkleTreeProofs() {
    const { account } = useEthers()
    const [data, setData] = useState([]);
    const getData = () => {
        fetch('json/welcomelist.json'
            , {
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            }
        )
            .then(function (response) {
                // console.log(response)
                return response.json();
            })
            .then(function (myJson) {
                // console.log(myJson);
                setData(myJson)
            });
    }
    useEffect(() => {
        getData()
    }, [])
    const proof = useMemo(()=> computeProof(account, data), [data, account]);
    return proof;
}
