import React from 'react'
import { useMintStats } from '../hooks/useMintStats'
import { useMintPrice } from '../hooks/useMintPrice'
import { formatEther } from '@ethersproject/units'
import MintTx from './MintTx';
export default function MintStats() {
    const stats = useMintStats();
    const percentage = Math.round(Number(stats.minted) / Number(stats.totalSupply) * 100);
    const widthPercentage = percentage.toString() + "%";
    const mintPrice = useMintPrice();
    return (
        <React.Fragment>
            {/* <br />
            <br />
            <br /> */}
            <div class="pt-16 pb-20 px-14 bg-blue-500 rounded-3xl">
                <div class="text-center">
                    <div class="text-white border-gray-400">
                        <h3 class="mb-3 text-4xl text-center text-lg font-bold font-heading text-white">Current Price</h3>
                        <p class="py-3 text-2xl text-center rounded-l font-bold text-white bg-gray-700">{formatEther(mintPrice)} ETH</p>
                    </div>
                </div>
                <br />
                <div class="w-full bg-gray-200 rounded-full">
                    <div class="bg-blue-600 text-xl font-medium text-green-100 text-center p-0.5 leading-none rounded-l-full" style={{ width: widthPercentage }}> {percentage}%</div>
                </div>
                <div class="text-center">
                    <p class="text-lg mb-10 lg:px-20 text-white">{stats.minted}/{stats.totalSupply} Minted</p>
                    <br />
                    {<MintTx />/* <a class="inline-block text-3xl text-center px-12 py-4 text-white font-bold border border-white hover:border-gray-100 rounded-full" href="#">Mint Now</a> */}
                    <br />

                </div>
            </div>
        </React.Fragment>
    )
}