import { BigNumber, Contract, ethers } from "ethers";
import { useCall } from "@usedapp/core";
import {useLeagueContract} from './useLeagueContract';
export function useMintStats() {
    const stats = useCall({
        contract: useLeagueContract(),
        method: "getCurrentDropStats",
        args: [],
    }) ?? [];
    if (stats.value) {
        return { dropId: stats.value[0], totalSupply: stats.value[1].toString(), minted: stats.value[2].toString()};
    } else {
        return { dropId: -1, totalSupply: 0, minted: 0};
    }
}