import React, { useMemo, useState } from 'react'
import { useEthers, useContractFunction } from '@usedapp/core'
import { useMintPrice } from '../hooks/useMintPrice';
import { useSalesInfo } from '../hooks/useSalesInfo';
import { useMerkleTreeProofs } from '../hooks/useMerkleTreeProofs';
import { useWalletUSDBalance } from '../hooks/useWalletUSDBalance';
import { useLeagueContract } from '../hooks/useLeagueContract';
function currencyFormat(num) {
    return '$' + num.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
}
let lastChecked = 0;
export default function MintTx() {
    const { account, activateBrowserWallet } = useEthers()
    const contract = useLeagueContract()
    const mintPrice = useMintPrice()
    const salesStats = useSalesInfo()
    salesStats.whitelistEnd = Math.floor(new Date().getTime() / 1000) + 20000
    const balanceUSD = currencyFormat(Number(useWalletUSDBalance() / 100))
    // salesStats.whitelistStart = 1654462132;
    // salesStats.dutchStart = 1656462132;
    if (lastChecked === 0 || lastChecked - Math.floor(new Date().getTime() / 1000) > 2000) {
        lastChecked = Math.floor(new Date().getTime() / 1000)
    }
    const [isWhitelistOnly, setIsWhitelistOnly] = useState(false)
    if (lastChecked > Number(salesStats.whitelistStart) && lastChecked < Number(salesStats.whitelistEnd)) {
        if (!isWhitelistOnly) {
            setIsWhitelistOnly(true);
        }
    }
    const [isDutchAuction, setIsDutchAuction] = useState(false)

    if (!isWhitelistOnly && lastChecked > Number(salesStats.dutchStart)) {
        if (!isDutchAuction) {
            setIsDutchAuction(true)
        }
    }
    // isDutchAuction = true;
    // isWhitelistOnly = false;
    const WhitelistMintComponent = () => {
        const { whitelisted, proof } = useMerkleTreeProofs()
        const functionName = 'whitelistedMintLeagueNFT'
        const { state, send } = useContractFunction(contract, functionName)

        const { status } = state
        // const { statusWhitelist } = stateWhitelist
        const mintNFT = () => {
            void send(proof, { value: mintPrice })
        }

        return (
            <React.Fragment>

                {!whitelisted ? (<button class="inline-block text-xl text-center px-12 py-4 text-white font-bold border border-white hover:border-gray-100 rounded-full" >Wallet Is Not in the Welcome list</button>) :
                    (<div>
                        <button class="inline-block text-2xl text-center px-12 py-4 text-white font-bold bg-green-700 border border-white hover:border-gray-100 rounded-full" onClick={() => mintNFT()}><p>Mint Now (Welcome-List Only)</p>
                        </button>
                        <p>Status: {status}</p></div>
                    )
                }


            </React.Fragment>
        )

    }
    const MintComponent = () => {
        let functionName = 'mintLeagueNFT';
        const { state, send } = useContractFunction(contract, functionName)

        const { status } = state
        // const { statusWhitelist } = stateWhitelist
        const mintNFT = () => {
            void send({ value: mintPrice })
        }

        return (
            <React.Fragment>
                <div>
                    <button class="inline-block text-3xl text-center px-12 py-4 text-white bg-green-700 font-bold border border-white hover:border-gray-100 rounded-full" onClick={() => mintNFT()}> Mint Now
                    </button>
                    <p>Status: {status}</p>
                </div>
            </React.Fragment>
        )

    }
    return (
        <div>
            {!account ?
                <button class="inline-block text-3xl text-center px-12 py-4 text-white font-bold border border-white hover:border-gray-100 rounded-full" onClick={() => activateBrowserWallet()}>
                    Connect wallet</button> :
                (
                    isDutchAuction ?
                        (<MintComponent />
                        ) : isWhitelistOnly ? (<WhitelistMintComponent />) :
                            (
                                <button class="inline-block text-3xl text-center px-12 py-4 text-white font-bold border border-white hover:border-gray-100 rounded-full">No Mint Possible Yet</button>
                            )
                )

            }
            {account ? (<p class="inline-block text-xl text-center px-12 py-4 text-white border-white hover:border-gray-100 rounded-full">
                Accepted Balance: {balanceUSD}</p>) : (<p />)
            }
        </div>
    )

}