import { useCall } from "@usedapp/core";
import { useLeagueContract } from './useLeagueContract';
export function useSalesInfo() {
    const stats = useCall({
        contract: useLeagueContract(),
        method: "getSalesInfo",
        args: [],
    }) ?? [];
    if (stats.value) {
        // uint256 whitelistStart,
        // uint256 whitelistEnd,
        // uint256 dutchStart,
        // uint256 dutchElapsed,
        // uint256 dutchInterval,
        // uint256 dutchEnd,
        // uint256 dutchDrop,
        // uint256 dutchMin,
        // uint256 dutchStartPrice
        return {
            whitelistStart: stats.value[0].toString(),
            whitelistEnd: stats.value[1].toString(),
            dutchStart: stats.value[2].toString(),
            dutchElapsed: stats.value[3].toString(),
            dutchInterval: stats.value[4].toString(),
            dutchEnd: stats.value[5].toString(),
            dutchDrop: stats.value[6].toString(),
            dutchMin: stats.value[7].toString(),
            dutchStartPrice: stats.value[8].toString()
        };
    } else {
        return {
            whitelistStart: "0",
            whitelistEnd: "0",
            dutchStart: "0",
            dutchElapsed: "0",
            dutchInterval: "0",
            dutchEnd: "0",
            dutchDrop: "0",
            dutchMin: "0",
            dutchStartPrice: "0"
        };
    }
}