import { BigNumber } from "ethers";
import { useCall } from "@usedapp/core";
import { useLeagueContract } from './useLeagueContract';
export function useMintPrice() {
    let mintPrice = BigNumber.from("0").toString();
    const mintStat = useCall({
        contract: useLeagueContract(),
        method: "getCurrentMintPrice",
        args: [],
    }) ?? [];
    if (mintStat.value) { mintPrice = mintStat.value.toString(); }
    return mintPrice;
}