
import { Contract, ethers } from "ethers";
import leagueContractAbi from "../abi/League.json";
import settingsData from '../settings.json';
import { useEthers } from "@usedapp/core";
import {useMemo} from 'react';



export function useLeagueContract() {

        const { chainId } = useEthers()
        const leagueContractInterface = useMemo(()=> new ethers.utils.Interface(leagueContractAbi),[]);
        const contract = new Contract(settingsData["Contracts"][chainId].LEAGUE_CONTRACT_ADDRESS, leagueContractInterface)
        return contract;
}