import { BigNumber } from "ethers";
import { useCall } from "@usedapp/core";
import { useLeagueContract } from './useLeagueContract';
import { useEthers } from "@usedapp/core";
export function useWalletUSDBalance() {
    let balanceUSD = BigNumber.from("0").toString();
    const { account } = useEthers()
    const ret = useCall({
        contract: useLeagueContract(),
        method: "UserWorthInUSD",
        args: [account],
    }) ?? [];
    // console.log(stats.value);
    if (ret.value) { 
        balanceUSD = ret.value.toString(); 
    }
    return balanceUSD;
}