
import React, { useState } from 'react'
import { formatEther } from '@ethersproject/units'
import { useSalesInfo } from '../hooks/useSalesInfo';
import Moment from 'react-moment';


export default function DutchInfo() {
    const stats = useSalesInfo();
    let nextDrop;
    // const [ nextDrop, setNextDrop ] = useState(0);
    const intervalHour = Math.floor(stats.dutchInterval / 3600);
    const intervalMinute = Math.floor(stats.dutchInterval % 3600 / 60);
    const nowTs = Math.floor(new Date().getTime() / 1000)
    const diffTime = nowTs - Number(stats.dutchStart);

    let dutchEnded = false;
    if (nowTs > Number(stats.dutchEnd)) {
        dutchEnded = true;
    }
    if (diffTime > 0) {
        const passedIntervals = Math.ceil(diffTime / Number(stats.dutchInterval));
        // setNextDrop(passedIntervals * Number(stats.dutchInterval) + Number(stats.dutchStart));
        nextDrop = passedIntervals * Number(stats.dutchInterval) + Number(stats.dutchStart);
    } else {
        // setNextDrop(Number(stats.dutchInterval) + Number(stats.dutchStart));
        nextDrop = Number(stats.dutchInterval) + Number(stats.dutchStart);
    }

    return (
        <React.Fragment>
            {/* <div class="pt-16 pb-20 px-14 rounded-3xl"> */}
            <div class="flex">
                {/* <img class="mr-6 h-20" src="zospace-assets/images/new-3d-icon.svg" alt="" /> */}
                <div>
                    <h3 class="mb-3 text-2xl font-bold font-heading text-white">313 Drop Welcome-List + Dutch Auction</h3>
                    <p class="text-l font-bold text-blue-400">Welcome-List Sale Start:</p>
                    <p class="text-lg text-center font-bold text-white border-b"><Moment local unix>{stats.whitelistStart}</Moment></p>
                    <p class="text-l font-bold text-blue-400">Starting Price:</p>
                    <p class="text-lg text-center  font-bold text-white border-b">1 ETH</p>
                    <p class="text-l font-bold text-blue-400">Dutch Auction Sale Start:</p>
                    <p class="text-lg text-center font-bold text-white border-b"><Moment local unix>{stats.dutchStart}</Moment></p>
                    <p class="text-l font-bold text-blue-400">Price Drops By:</p>
                    <p class="text-lg text-center font-bold text-white border-b">{formatEther(stats.dutchDrop)} until sold out or reaches {formatEther(stats.dutchMin)} ETH floor</p>
                    <p class="text-l font-bold text-blue-400">Price Drop Frequency:</p>
                    <p class="text-lg text-center font-bold text-white border-b">Every {intervalHour}h:{intervalMinute}m</p>
                    <p class="text-l font-bold text-blue-400">Max Mint Quantity:</p>
                    <p class="text-lg text-center font-bold text-white border-b">1 per wallet with a balance over one million USD (in ETH, DAI, UDSC, or USDT) </p>
                    {/* for the first <Moment unix local unit="days" duration={stats.whitelistStart} date={stats.dutchStart}/>. Then 1 per any wallet with a balance over one million USD (in ETH, DAI, UDSC, or USDT) starting <Moment local unix>{stats.dutchStart}</Moment></p> */}
                    {/* <p class="text-l font-bold text-blue-400">End Time:</p> */}
                    {/* <p class="text-lg text-center font-bold text-white border-b">7 days</p> */}
                </div>
            </div>
            <ul class="text-white border-gray-400">
                <li class="py-4 text-lg font-bold text-blue-400">Next Price Drop</li>
                <li class="py-3 text-6xl text-center  rounded-2xl font-bold text-blue-500 bg-gray-700">
                    {!dutchEnded ?
                        (
                            <Moment unix local format='ddd HH:mm'>{nextDrop}</Moment>
                        ) :
                        (<p>Auction Ended</p>
                        )}
                </li>
            </ul>
            {/* </div> */}
        </React.Fragment>
    )
}