import React from 'react'
import GalleryItem from './GalleryItem'
export default function Gallery() {
    var cards = [];
    for(var i=1;i<=313;i++){
        cards.push( 
            <GalleryItem data={i}></GalleryItem>
        );
    }    
    return (
        <React.Fragment>
            <section class="relative py-20 2xl:py-40 bg-gray-800 overflow-hidden">
            <div class="mb-10 max-w-xl mx-auto mb-24 text-center">
                    <h2 id="gallery" style={{'z-index':'999;'}} class="mt-12 text-5xl lg:text-6xl text-white font-bold font-heading">313 Drop Gallery</h2>
                </div>
                <img class="hidden lg:block absolute top-0 left-0 h-screen -ml-96" src="zospace-assets/lines/circle.svg" alt="" />
                <img class="hidden lg:block absolute top-0 right-0 mt-40 -mr-64" src="zospace-assets/lines/circle.svg" alt="" />
     
                <div class="relative container px-4 mx-auto">
                    <div>
                        <div class="flex flex-wrap -m-5">
                        {cards}
                        </div>
                    </div>
                </div>
            </section>
        </React.Fragment>
    )
}